@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap');

$red: #ff3838;


* {
    font-family: 'Nunito', sans-serif;
    margin:0; padding:0;
    box-sizing: border-box;
    outline: none; border:none;
    text-decoration: none;
    text-transform: capitalize;
    transition:all .2s linear;
 
    &::selection{
        background: $red;
        color:#fff;
    }
}
  
html {
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    scroll-padding-top: 6rem;
}
  
body{
    background:#f7f7f7;
}
  
section{
    padding:2rem 9%;
}

.header{
    position: fixed;
    top:0; left: 0; right:0;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background:#fff;
    padding:2rem 9%;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
  
    &__logo {
        font-size: 2.5rem;
        font-weight: bolder;
        color:#666;

        & i {
            padding-right: .5rem;
            color: $red;
        }
    }

    &__navbar {

        & a {
            font-size: 2rem;
            margin-left: 2rem;
            color:#666;

            &:hover {
                color: $red;
            }
        }
    }

    #menu-bar {
        font-size: 3rem;
        cursor: pointer;
        color:#666;
        border:.1rem solid #666;
        border-radius: .3rem;
        padding:.5rem 1.5rem;
        display: none;
    }
}
  
  
  
